.word {
  display: inline-block;
  position: relative;
  transition: transform 700ms ease-in-out;
}
.correct {
  color: #6aa84f;
  font-weight: 500;
}
.incorrect {
  color: #797270;
  font-weight: 400;
}
.wordSelected {
  text-decoration: underline solid #453f3c;
  -webkit-text-decoration: underline solid #453f3c;
  color: #453f3c;
}
.wordSwap {
  transform: translate(var(--transLeft), var(--transTop));
}
.nowrap {
  white-space: nowrap;
}
.punctuation {
}
a:link {
  text-decoration: none;
}
a:visited {
  text-decoration: none;
}
a:hover {
  text-decoration: none;
}
a:active {
  text-decoration: none;
}
